.modal_wraper {
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.4);
}
.modal_content {
  max-width: 100%;
  display: flex;
  flex-direction: column;
  margin: 0;
  position: relative;
  border-radius: 8px;
  background: #fcfcfc;
  box-shadow: 0 4px 16px 0 rgba(22, 22, 22, 0.05);
  padding: 24px;
}
.btn_close {
  border: none;
  position: absolute;
  top: 12px;
  right: 12px;
  padding: 0;
  background: none;
  stroke: black;
  cursor: pointer;
}

.item_svg {
  width: 18px;
  height: 18px;
}
.item_use {
   stroke: black;
   fill: transparent;
}

.titel {
  margin-bottom: 24px;
  font-weight: 500;
  font-size: 18px;
  letter-spacing: -0.02em;
  color: #161616;
}

@media (min-width: 375px) {
  .modal_content {
    max-width: 335px;
  }
}

@media (min-width: 768px) and (max-width: 1439px) {
  .modal_content {
    max-width: 350px;
  }
}

